<template>
  <div class="Staff">
    <Nav />

    <h1 class="GroupTitle">Active Staff</h1>

    <div
      v-for="group in groups"
      v-if="group.active"
      :key="group.id"
      @click="GroupChecked(group)"
      :class="{ activeGroup: group.active }"
      class="ButtonContainer grid"
    >
      <h1>{{group.name}}</h1>
      <div class="circle" v-bind:style="{ backgroundColor: group.colour }" />
      <p>Kids: {{group.kids}} Staff: {{group.staff}}</p>
    </div>

    <h1 class="GroupTitle">All Staff</h1>

    <div
      v-for="group in groups"
      v-if="!group.active"
      :key="group.id"
      @click="GroupChecked(group)"
      :class="{ activeGroup: group.active }"
      class="ButtonContainer grid"
    >
      <h1>{{group.name}}</h1>
      <div class="circle" v-bind:style="{ backgroundColor: group.colour }" />
      <p>Kids: {{group.kids}} Staff: {{group.staff}}</p>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import { log } from "util";

export default {
  name: "Staff",
  components: {
    Nav
  },
  data: function() {
    return {
      groups: [
        {
          name: "Pink",
          colour: "Red",
          kids: 7,
          staff: 3,
          active: false
        },
        {
          name: "Blue",
          colour: "Blue",
          kids: 7,
          staff: 3,
          active: true
        },
        {
          name: "Yellow",
          colour: "Yellow",
          kids: 7,
          staff: 3,
          active: false
        }
      ]
    };
  },
  methods: {
    GroupChecked(group) {
      group.active = !group.active;
    }
  }
};
</script>

<style scoped>
h1 {
  float: left;
  place-self: left;
  text-align: left;
  margin-left: 1em;
  margin-top: 0.5em;
}
p {
  text-align: left;
  margin-left: 2.1em;
  opacity: 0.3;
  margin-top: 1.5em;
}
.GroupTitle {
  background-color: rgba(9, 32, 82, 0);
  padding: 1em;
  height: 2em;
  width: 100%;
  margin-top: 1em;
  margin-left: 0em;
  text-align: left;
  color: pink;
}
.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 2em);
  padding-top: 2em;
  padding-bottom: 0em;
  justify-content: end;
}
.circle {
  grid-row: span 2;
  float: right;
  place-self: right;
  text-align: right;
}

.activeGroup .circle {
  background-color: green;
}

.ButtonContainer {
  width: 90%;
  transition: 0.4s;
}

.activeGroup {
  width: 70%;
}
</style>
