<template>
  <div class="Checklist">
    <Nav />
    <div class="GroupTitle">
      <h1>Pink Group</h1>
    </div>

    <div
      v-for="kid in kids"
      :key="kid.id"
      @click="check(kid)"
      :class="{ checked: kid.checked }"
      class="ButtonContainer grid"
    >
      <h1>{{kid.name}}</h1>
      <div class="circle" />
      <p>Age: {{kid.age}}</p>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import { log } from "util";

export default {
  name: "Checklist",
  components: {
    Nav
  },
  data: function() {
    return {
      kids: [
        {
          name: "Girl 01",
          age: 7,
          group: "pink",
          checked: false
        },
        {
          name: "Girl 02",
          age: 8,
          group: "pink",
          checked: false
        },
        {
          name: "Boy 01",
          age: 9,
          group: "pink",
          checked: false
        }
      ]
    };
  },
  methods: {
    check(kid) {
      kid.checked = !kid.checked;
    }
  }
};
</script>

<style>
h1 {
  float: left;
  text-align: left;
}
p {
  text-align: left;
}
.GroupTitle {
  background-color: rgba(9, 32, 82, 0);
  padding: 2em;
  height: 3em;
  margin-top: 1em;
  color: pink;
}
.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 2em);
  padding-top: 2em;
  padding-bottom: 0em;
  justify-items: center;
}
.circle {
  grid-row: span 2;
}

.checked .circle {
  background-color: green;
}

.checked {
  width: 40%;
}

.ButtonContainer {
  opacity: 0.5;
}

.ButtonContainer:hover {
  width: 50%;
  opacity: 1;
}
</style>