<template>
  <div class="Map MapAPI"></div>
</template>

<script>
import gmapsInit from "../utils/gmaps";
const axios = require("axios");

export default {
  name: "Map",
  data: function() {
    return {
      map: null,
      markers: {}
    };
  },
  sockets: {
    connect() {
      // Fired when the socket connects.
      this.isConnected = true;
    },

    disconnect() {
      this.isConnected = false;
    },

    // Fired when the server sends something on the "messageChannel" channel.
    childMove(data) {
      data = JSON.parse(data);
      console.log(this.map);
      if (data.id in this.markers) {
        this.markers[data.id].setPosition({ lat: data.lat, lng: data.lng });
      } else {
        this.markers[data.id] = new google.maps.Marker({
          position: { lat: data.lat, lng: data.lng },
          map: this.map,
          title: data.id,
          icon: {
            path: google.maps.SymbolPath.BACKWARD_CLOSED_ARROW,
            scale: 5,
            strokeColor: "gold"
          }
        });
      }
    }
  },

  methods: {
    pingServer() {
      // Send the "pingServer" event to the server.
      this.$socket.emit("pingServer", "PING!");
    }
  },
  async mounted() {
    try {
      const google = await gmapsInit();
      const geocoder = new google.maps.Geocoder();
      const map = new google.maps.Map(this.$el, {
        zoom: 17,
        center: { lat: 58.852271, lng: 5.739428 },
        disableDefaultUI: true,
        mapTypeId: "satellite"
      });

      var drawingManager = new google.maps.drawing.DrawingManager({
        drawingMode: google.maps.drawing.OverlayType.MARKER,
        drawingControl: true,
        drawingControlOptions: {
          position: google.maps.ControlPosition.TOP_CENTER,
          drawingModes: ["polygon"]
        },
        markerOptions: {
          icon:
            "https://developers.google.com/maps/documentation/javascript/examples/full/images/beachflag.png"
        },
        circleOptions: {
          fillColor: "#ffff00",
          fillOpacity: 0.5,
          strokeWeight: 5,
          clickable: false,
          editable: true,
          zIndex: 1
        },
        polygonOptions: {
          fillColor: "#ffff00",
          strokeColor: "#ffff00",
          fillOpacity: 0.8,
          strokeWeight: 5,
          clickable: false,
          editable: true,
          zIndex: 1
        }
      });
      drawingManager.setMap(map);

      google.maps.event.addListener(drawingManager, "polygoncomplete", function(
        poly
      ) {
        let GeoJSON = {
          id: "fence" + 1000000 * Math.random(),
          data: {
            type: "Polygon",
            coordinates: []
          }
        };
        for (let point of poly.getPath().getArray()) {
          GeoJSON.data.coordinates.push([point.lng(), point.lat()]);
        }
        axios
          .post("http://localhost:9090/addfence", GeoJSON)
          .then(function(response) {
            // handle success
            for (let [key, value] of Object.entries(response.data)) {
              map.data.add({
                geometry: new google.maps.Data.Polygon(value)
              });
            }
          })
          .catch(function(error) {
            // handle error
            console.log(error);
          })
          .finally(function() {
            // always executed
          });
        console.log(GeoJSON);
      });

      this.map = map;
      /*geocoder.geocode({ address: "Austria" }, (results, status) => {
        if (status !== "OK" || !results[0]) {
          throw new Error(status);
        }
        map.setCenter(results[0].geometry.location);
        map.fitBounds(results[0].geometry.viewport);
      });*/
    } catch (error) {
      console.error(error);
    }
  }
};
</script>

<style>
html,
body {
  margin: 0;
  padding: 0;
}

.Map {
  width: 100vw;
  height: 100vh;
}
</style>
